.mainWrap {
  display: flex; 
  color: #ffffff; 

}

.contentWrap {
  width: 100%;
}

.contentContainer {
  display: flex; 
padding: 2rem; 
flex-direction: column; 
justify-content: center; 
height: 100vh; 
}

.textContainer1 {
  color: #000000; 
background-color: #ffffff; 
}

.textContainer2 {
  color: #000000; 
/* background: linear-gradient(#ffffff 0%, #1A2229 95%); */
}

.contentTitleText {
  font-size: 1.875rem;
line-height: 2.25rem; 
font-weight: 500; 
text-transform: uppercase;
}

.contentDescText {
  width: 100%; 
max-width: 28rem; 
font-weight: 300;
}

.imageContainer {
  overflow: hidden; 
position: sticky; 
top: 0; 
width: 6rem; 
height: 100vh; 


@media (min-width: 768px) { 
  width: 100%; 
 }
}

.imagePosition {
  position: absolute; 
right: 0; 
left: 0; 
}

.image {
  object-fit: contain; 
width: 100%; 
height: 100vh; 

}